.holiday {
    .dashboardBody {
        display: flex;

        .dashboardContent {
            padding: 20px 15px;
            width: calc(100% - 230px);

            .headerBody {
                display: flex;
                justify-content: space-between;
                align-items: center;

                .actionHeader {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 20px;
                }
                .tableBtn {
                    color: #FFF;
                    font-size: 16px;
                    font-weight: 500;
                    letter-spacing: 0.16px;
                    background-color: #4367B0;
                    padding: 0 10px;
                    height: 45px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 5px;
                    border: none;
                    border-radius: 35px;
                }

                .title {
                    color: #000;
                    font-size: 28px;
                    font-weight: 600;
                    letter-spacing: 0.28px;
                }
            }

            .headerInputs {
                display: flex;
                gap: 20px;
                flex-wrap: wrap;
                // margin-top: 30px;
                margin: 30px 0;

                
                .formInput {
                    width: 30%;

                    label {
                        color: #808080;
                        font-size: 16px;
                        font-weight: 500;
                        letter-spacing: 0.16px;

                        span {
                            color: #D80032;
                        }
                    }

                    input,select {
                        color: #4367B0;
                        width: 100%;
                        padding: 13px 24px;

                        border: 1px solid #BFBFBF;
                        border-radius: 36px;
                        background: #FFF;
                        font-size: 16px;
                        font-weight: 400;
                        letter-spacing: 0.16px;
                        margin-top: 5px;
                    }

                    input::placeholder {
                        color: #BFBFBF;
                    }

                    input:focus {
                        outline: 1px solid #4367B0;
                    }
                }

                .filterContainer {
                    display: flex;
                    flex-direction: column;
                    gap: 20px;
                    width: 30%;
                    
                    .formInput {
                        width: 100%;
                        
                    }

    
    
                    .filters {
                        display: flex;
                        flex-wrap: wrap;
                        gap: 10px;
    
                        .filterItem {
    
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            padding: 10px;
                            font-size: 14px;
                            color: #4367B0;
                            border-radius: 40px;
                            border: 1px solid #4367B0;
                            gap: 5px;
    
    
                        }
                    }
                }
            }

            .formInput {
                label {
                    color: #808080;
                    font-size: 16px;
                    font-weight: 500;
                    letter-spacing: 0.16px;

                    span {
                        color: #D80032;
                    }
                }

                input,select {
                    color: #4367B0;
                    width: 100%;
                    padding: 13px 24px;

                    border: 1px solid #BFBFBF;
                    border-radius: 36px;
                    background: #FFF;
                    font-size: 16px;
                    font-weight: 400;
                    letter-spacing: 0.16px;
                    margin-top: 5px;
                }

                input::placeholder {
                    color: #BFBFBF;
                }

                input:focus {
                    outline: 1px solid #4367B0;
                }
            }

            .holidayContainer {
                border-radius: 14px;
                // border: 1px solid #E6E6E6;
                background-color: #FFF;
                // padding: 25px;
                display: flex;
                flex-direction: column;
                gap: 20px;
                margin-top: 20px;

                .employeeDetails {
                    border-radius: 15px;
                    border: 1px solid var(--Secondary-25, #BFBFBF);
                    width: 100%;


                    table {
                        border-collapse: collapse;
                        border-spacing: 0;
                        // width: 100em;
                        width: 100%;
                    }

                    thead {

                        tr th {
                            color: #808080;
                            padding: 16px 12px;
                            font-size: 16px;
                            font-weight: 400;
                            letter-spacing: 0.2px;
                            white-space: nowrap;
                            text-align: left;
                            border: none !important;
                            white-space: nowrap;
                        }

                        
                        tr {
                            border-bottom: 1px solid var(--Secondary-25, #BFBFBF);

                        }

                    }

                    tbody {

                        tr td {
                            color: #000;
                            font-size: 16px;
                            font-weight: 400;
                            letter-spacing: 0.16px;
                            white-space: nowrap;
                            padding: 20px 12px;
                            text-align: left;
                            white-space: nowrap;


                        }

                        tr:nth-child(odd) {
                            background-color: #F4F6FB;

                        }

                        tr {
                            border-bottom: 1px solid var(--Secondary-25, #BFBFBF);

                        }

                        tr:last-child {
                            border-bottom: none;
                        }

                        .employeeTable {
                            width: 100%;

                        }

                        .actionLink a {
                            color: #4367B0;
                            font-weight: 600;
                        }
                    }
                }





            }
        }
    }
}
