.addHoliday {
    .addHolidayBody {
        margin-top: 40px;
        padding: 40px;
        display: flex;
        flex-direction: column;

        h2 {
            font-size: 28px;
            font-weight: 700;
        }
        .topNavigation {
            display: flex;
            border-radius: 10px;

            .item {
                color: #A1B3D8;
                font-size: 22px;
                font-weight: 500;
                letter-spacing: 0.16px;
                padding: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
                position: relative;
            }

            .active::after {
                content: "";
                width: 60%;
                height: 5px;
                position: absolute !important;
                bottom: 0;
                display: block;
                background-color: #4367B0;
                border-radius: 10px 10px 0 0;
            }

            .active {
                color: #4367B0;
            }
        }




        .addHolidayContainer {
            border-radius: 14px;
            border: 1px solid #bfbfbf;
            background-color: #FFF;
            padding: 25px;
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            gap: 40px;

            .headerInputs {
                display: flex;
                gap: 20px;
                flex-wrap: wrap;

                
                .formInput {
                    width: 30%;

                    label {
                        color: #808080;
                        font-size: 16px;
                        font-weight: 500;
                        letter-spacing: 0.16px;

                        span {
                            color: #D80032;
                        }
                    }

                    input,select {
                        color: #4367B0;
                        width: 100%;
                        padding: 13px 24px;

                        border: 1px solid #BFBFBF;
                        border-radius: 36px;
                        background: #FFF;
                        font-size: 16px;
                        font-weight: 400;
                        letter-spacing: 0.16px;
                        margin-top: 5px;
                    }

                    input::placeholder {
                        color: #BFBFBF;
                    }

                    input:focus {
                        outline: 1px solid #4367B0;
                    }
                }

                .filterContainer {
                    display: flex;
                    flex-direction: column;
                    gap: 20px;
                    width: 30%;
                    
                    .formInput {
                        width: 100%;
                        
                    }

    
    
                    .filters {
                        display: flex;
                        flex-wrap: wrap;
                        gap: 10px;
    
                        .filterItem {
    
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            padding: 10px;
                            font-size: 14px;
                            color: #4367B0;
                            border-radius: 40px;
                            border: 1px solid #4367B0;
                            gap: 5px;
    
    
                        }
                    }
                }
            }

            .bodyNavigation {
                border-radius: 16px;
                display: inline-flex;

                .title {
                    padding: 12px 20px;
                    border-right: 1px solid #4367B0;
                    font-size: 16px;
                    font-weight: 500;
                    border: 1px solid #4367B0;
                    cursor: pointer;
                }

                .title:first-child {
                    border-radius: 16px 0 0 16px;
                    border-right: 0;

                }

                .title:last-child {
                    border-radius: 0 16px 16px 0;
                    border-left: 0;
                }

                .active {
                    color: #fff;
                    background-color: #4367B0;
                }
            }

            .formActions {
                display: flex;
                align-items: flex-end;
                justify-content: flex-start;
                gap: 30px;

                .add, .delete {
                    color: #4367B0;
                    font-size: 16px;
                    font-weight: 500;
                    display: flex;
                    gap: 5px;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                }

                .delete {
                    color: #D80032;
                }
            }

            .innerContainer {
                display: flex;
                justify-content: space-between;
                width: 100%;
                align-items: center;


                .formFields {
                    display: flex;
                    gap: 20px;
                    // width: 100%;
                    flex-wrap: wrap;
                    flex: 1;
                    width: 80%;
                    align-items: center;
    
                    .formInput {
                        width: 20%;
                    }

                    .checkboxInput {
                        display: flex;
                        align-items: center;
                        gap: 5px;

                        input{
                            width: 15px;
                            height: 15px;
                            margin-top: 0;
                        }
                    }
    
    
                    label {
                        color: #808080;
                        font-size: 16px;
                        font-weight: 500;
                        letter-spacing: 0.16px;
    
                        span {
                            color: #D80032;
                        }
                    }
    
                    input {
                        color: #4367B0;
                        width: 100%;
                        padding: 13px 24px;
    
                        border: 1px solid #BFBFBF;
                        border-radius: 36px;
                        background: #FFF;
                        font-size: 16px;
                        font-weight: 400;
                        letter-spacing: 0.16px;
                        margin-top: 5px;
                    }
    
                    input::placeholder {
                        color: #BFBFBF;
                    }
    
                    input:focus {
                        outline: 1px solid #4367B0;
                    }
    
                }
            } 

            .nextBtn {
                border-radius: 52px;
                background: var(--Primary-100, #4367B0);
                color: var(--White-100, #FFF);
                text-align: center;
                font-size: 20px;
                font-weight: 500;
                letter-spacing: 0.2px;
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 10px;
                border: none;
                width: 150px;
                height: 56px;
            }

            .bulkUpload {
                display: flex;
                flex-direction: column;
                gap: 20px;
                .employeeDetails {
                    border-radius: 15px;
                    border: 1px solid var(--Secondary-25, #BFBFBF);
                    width: 90%;
    
    
                    table {
                        border-collapse: collapse;
                        border-spacing: 0;
                        // width: 100em;
                        width: 100%;
                    }
    
                    thead {
    
                        tr th {
                            color: #808080;
                            padding: 16px 12px;
                            font-size: 16px;
                            font-weight: 400;
                            letter-spacing: 0.2px;
                            white-space: nowrap;
                            text-align: left;
                            border: none !important;
                        }
    
                    }
    
                    tbody {
                        background-color: #F4F6FB;
    
                        tr td {
                            color: #000;
                            font-size: 16px;
                            font-weight: 400;
                            letter-spacing: 0.16px;
                            white-space: nowrap;
                            padding: 20px 12px;
                            text-align: left;
    
    
                        }
    
                    }
    
                    tr {
                        border-bottom: 1px solid var(--Secondary-25, #BFBFBF);
    
                    }
    
                    .employeeTable {
                        width: 100%;
    
                    }
                }
    
                .uploadContent {
                    .title {
                        color: var(--Secondary-100, #000);
                        font-size: 20px;
                        font-weight: 400;
                        letter-spacing: 0.2px;
                    }
                }
    
    
                .row {
                    display: flex;
                    gap: 10px;
    
                    .bullet {
                        color: var(--Secondary-100, #000);
                        text-align: center;
                        font-size: 20px;
                        font-weight: 600;
                        letter-spacing: 0.2px;
                    }
    
                    .firstTitle {
                        color: var(--Secondary-100, #000);
                        font-size: 20px;
                        font-weight: 400;
                        letter-spacing: 0.2px;
                    }
                }
    
                .first {
                    align-items: center;
                }
    
                .second {
                    h6 {
                        color: var(--Secondary-100, #000);
                        font-size: 20px;
                        font-weight: 600;
                        letter-spacing: 0.2px;
                    }
    
                    p {
                        color: var(--Secondary-100, #000);
                        font-size: 16px;
                        font-weight: 400;
                        letter-spacing: 0.16px;
                    }
                }
    
                .fourth {
                    .uploadArea {
                        width: 350px;
                        border-radius: 8px 8px 8px 8px;
                        border: 1px solid #BFBFBF;
                        background-color: #FFF;
                        display: flex;
                        flex-direction: column;
                        gap: 10px;
                        margin-top: 20px;
    
                        .titleHomework {
                            border-radius: 8px;
                            background: var(--Secondary-100, #000);
                            padding: 10px;
    
                            color: var(--White-100, #FFF);
                            font-size: 16px;
                            font-weight: 500;
                            letter-spacing: 0.16px;
                        }
    
                        .uploadInfo {
                            color: var(--Secondary-100, #000);
                            text-align: center;
                            font-size: 12px;
                            font-weight: 400;
                            line-height: 16px;
                            letter-spacing: 0.12px;
                        }
    
                        .uploadHomeworkBtn {
                            border-radius: 8px;
                            background: var(--Primary-100, #4367B0);
                            padding: 8px 12px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            gap: 4px;
    
                            color: var(--White-100, #FFF);
                            font-size: 16px;
                            font-weight: 500;
                            letter-spacing: 0.16px;
                            border: none;
                            width: 130px;
                            margin: 0 auto;
                        }
    
                        .uploadContainer {
                            padding: 15px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            flex-direction: column;
                            gap: 15px;
                        }
    
                        .fileNames {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            flex-direction: column;
                            gap: 10px;
    
                            .fileName {
                                color: var(--Primary-100, #4367B0);
                                text-align: center;
                                font-size: 16px;
                                font-weight: 500;
                                letter-spacing: 0.16px;
                                text-decoration-line: underline;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                gap: 20px;
    
                                .removeBtn {
                                    color: var(--Error-100, #D80032);
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    gap: 10px;
                                    text-decoration: none;
                                    cursor: pointer;
                                }
                            }
                        }
    
    
                    }
                }
    
                .downloadBtn {
                    a {
                        color: var(--White-100, #FFF);
                        text-align: center;
                        font-size: 16px;
                        font-weight: 600;
                        letter-spacing: 0.16px;
                        border-radius: 6px;
                        background: var(--Primary-100, #4367B0);
                        padding: 8px 16px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        gap: 5px;
                        border: none;
                    }
                }
            }



  



        }

    }
}