.login {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;

    .loginContainer {
        width: 500px;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        gap: 20px;
        border: 1px solid #BFBFBF;
        border-radius: 14px;
        padding: 35px;

        .title {
            color: #000;
            font-size: 36px;
            font-weight: 600;
            letter-spacing: 0.2px;
            text-align: left;
            width: 100%;
        }

        .loginForm {
            display: flex;
            flex-direction: column;
            gap: 25px;
            width: 100%;
            margin-top: 30px;


            label {
                color: #808080;
                font-size: 20px;
                font-weight: 500;
                letter-spacing: 0.2px;

                span {
                    color: #D80032;
                }
            }

            input {
                border-radius: 50px;
                font-size: 20px;
                font-weight: 400;
                color: #4367B0;
                letter-spacing: 0.2px;
                border: none;
                width: 100%;
                padding: 12px 24px;
                border: 1px solid #BFBFBF;
                margin: 5px 0;
            }

            input::placeholder {
                color: #BFBFBF;
            }

            input:focus {
                outline: 1px solid #000;
            }

            .inputUserName,
            .inputPass {
                position: relative;
                width: 100%;
            }

            .emailIcon,
            .passIcon {
                // width: 20x;
                position: absolute;
                left: 25px;
                top: 0;
                bottom: 0;
                margin: auto 0;
            }

            .eyeIcon {
                position: absolute;
                background-color: #808080;
                border-radius: 50%;
                padding: 10px;
                position: absolute;
                right: 25px;
                top: 0;
                bottom: 0;
                margin: auto 0;
                cursor: pointer;
            }
        }
        

        .bottomText {
            color: #808080;
            text-align: left;
            font-size: 14px;
            font-weight: 400;
            letter-spacing: 0.14px;
        }

        .actionBtns {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            margin-top: 30px;

            .resetPass {
                color: #4367B0;
                text-align: center;
                font-size: 16px;
                font-weight: 500;
                letter-spacing: 0.16px;
            }
    
            .loginBtn {
                padding: 15px;
                align-items: center;
                border-radius: 36px;
                background: #4367B0;
                color: #FFF;
                text-align: center;
                font-size: 20px;
                font-weight: 500;
                letter-spacing: 0.2px;
                border: none;
                position: relative;
                width: 150px;
    
                // .forwardArrow {
                //     position: absolute;
                //     background-color: #fff;
                //     border-radius: 50%;
                //     padding: 10px;
                //     position: absolute;
                //     right: 25px;
                //     top: 0;
                //     bottom: 0;
                //     margin: auto 0;
                // }
            }


        }

    }
}