.permissions {
    .dashboardBody {
        display: flex;

        .dashboardContent {
            padding: 20px 15px;
            width: calc(100% - 230px);

            .headerBody {
                display: flex;
                justify-content: space-between;
                align-items: center;

                .actionHeader {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 20px;
                }

                .tableBtn {
                    color: #FFF;
                    font-size: 16px;
                    font-weight: 500;
                    letter-spacing: 0.16px;
                    background-color: #4367B0;
                    padding: 0 10px;
                    height: 45px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 5px;
                    border: none;
                    border-radius: 35px;
                }

                .title {
                    color: #000;
                    font-size: 28px;
                    font-weight: 600;
                    letter-spacing: 0.28px;
                }
            }

            .formInput {
                label {
                    color: #808080;
                    font-size: 16px;
                    font-weight: 500;
                    letter-spacing: 0.16px;

                    span {
                        color: #D80032;
                    }
                }

                input,
                select {
                    color: #4367B0;
                    width: 100%;
                    padding: 13px 24px;

                    border: 1px solid #BFBFBF;
                    border-radius: 36px;
                    background: #FFF;
                    font-size: 16px;
                    font-weight: 400;
                    letter-spacing: 0.16px;
                    margin-top: 5px;
                }

                input::placeholder {
                    color: #BFBFBF;
                }

                input:focus {
                    outline: 1px solid #4367B0;
                }
            }

            .permissionsContainer {
                border-radius: 14px;
                border: 1px solid #e6e6e6;
                background-color: #fff;
                padding: 25px;
                display: flex;
                flex-direction: column;
                gap: 20px;
                margin-top: 20px;


                .bodyNavigation {
                    border-radius: 16px;
                    display: inline-flex;

                    .title {
                        padding: 12px 20px;
                        border-right: 1px solid #4367B0;
                        font-size: 16px;
                        font-weight: 500;
                        border: 1px solid #4367B0;
                        cursor: pointer;
                    }

                    .title:first-child {
                        border-radius: 16px 0 0 16px;
                        border-right: 0;

                    }

                    .title:last-child {
                        border-radius: 0 16px 16px 0;
                        border-left: 0;
                    }

                    .active {
                        color: #fff;
                        background-color: #4367B0;
                    }
                }

            }
        }
    }
}