.createEditDepartment {
    .dashboardBody {
        display: flex;

        .dashboardContent {
            padding: 20px 15px;
            width: calc(100% - 230px);

            .headerBody {
                display: flex;
                justify-content: space-between;
                align-items: center;

                .actionHeader {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 20px;
                }
                .tableBtn {
                    color: #FFF;
                    font-size: 16px;
                    font-weight: 500;
                    letter-spacing: 0.16px;
                    background-color: #4367B0;
                    padding: 0 10px;
                    height: 45px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 5px;
                    border: none;
                    border-radius: 35px;
                }

                .title {
                    color: #000;
                    font-size: 28px;
                    font-weight: 600;
                    letter-spacing: 0.28px;
                }
            }

            .formInput {
                label {
                    color: #808080;
                    font-size: 16px;
                    font-weight: 500;
                    letter-spacing: 0.16px;

                    span {
                        color: #D80032;
                    }
                }

                input,select {
                    color: #4367B0;
                    width: 100%;
                    padding: 13px 24px;

                    border: 1px solid #BFBFBF;
                    border-radius: 36px;
                    background: #FFF;
                    font-size: 16px;
                    font-weight: 400;
                    letter-spacing: 0.16px;
                    margin-top: 5px;
                }

                input::placeholder {
                    color: #BFBFBF;
                }

                input:focus {
                    outline: 1px solid #4367B0;
                }
            }

            .createDepartmentContainer {
                border-radius: 14px;
                border: 1px solid #E6E6E6;
                background-color: #FFF;
                padding: 25px;
                display: flex;
                flex-direction: column;
                gap: 20px;
                margin-top: 20px;

          
                .departmentContent {
                    display: flex;
                    justify-content: space-between;
                    gap: 40px;
    
                }

                .formActions {
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    gap: 30px;
    
                    .add, .delete {
                        color: #4367B0;
                        font-size: 16px;
                        font-weight: 500;
                        display: flex;
                        gap: 5px;
                        align-items: center;
                        justify-content: center;
                        cursor: pointer;
                    }
    
                    .delete {
                        color: #D80032;
                    }
                }
    
    
    
                .formFields {
                    display: flex;
                    gap: 20px;
                    // width: 100%;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    width: 60%;
    
                    .formInput {
                        width: 47%;
                    }
    
                    // .formInput:last-child {
                    //     width: 100%;
                    // }
    
                    label {
                        color: #808080;
                        font-size: 16px;
                        font-weight: 500;
                        letter-spacing: 0.16px;
    
                        span {
                            color: #D80032;
                        }
                    }
    
                    input {
                        color: #4367B0;
                        width: 100%;
                        padding: 13px 24px;
    
                        border: 1px solid #BFBFBF;
                        border-radius: 36px;
                        background: #FFF;
                        font-size: 16px;
                        font-weight: 400;
                        letter-spacing: 0.16px;
                        margin-top: 5px;
                    }
    
                    input::placeholder {
                        color: #BFBFBF;
                    }
    
                    input:focus {
                        outline: 1px solid #4367B0;
                    }
    
                }
    
                .nextBtn {
                    border-radius: 52px;
                    background: var(--Primary-100, #4367B0);
                    color: var(--White-100, #FFF);
                    text-align: center;
                    font-size: 20px;
                    font-weight: 500;
                    letter-spacing: 0.2px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 10px;
                    border: none;
                    width: 150px;
                    height: 56px;
                }




            }
        }
    }
}
