.adminDashboard {
    .dashboardBody {
        display: flex;

        .dashboardContent {
            padding: 20px 15px;
            width: calc(100% - 230px);

            .title,
            .bodyTitle {
                color: #000;
                font-size: 28px;
                font-weight: 600;
                letter-spacing: 0.28px;
            }

            .topInputFields {
                margin-top: 20px;


                .formFields {
                    display: flex;
                    gap: 20px;
                    // width: 100%;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    width: 100%;

                    .formInput {
                        width: 23%;
                    }


                    label {
                        color: #808080;
                        font-size: 16px;
                        font-weight: 500;
                        letter-spacing: 0.16px;

                        span {
                            color: #D80032;
                        }
                    }

                    input,
                    select {
                        color: #4367B0;
                        width: 100%;
                        padding: 13px 24px;

                        border: 1px solid #BFBFBF;
                        border-radius: 36px;
                        background: #FFF;
                        font-size: 16px;
                        font-weight: 400;
                        letter-spacing: 0.16px;
                        margin-top: 5px;
                    }

                    input::placeholder {
                        color: #BFBFBF;
                    }

                    input:focus {
                        outline: 1px solid #4367B0;
                    }


                    .searchBtns {
                        display: flex;
                        gap: 30px;
                        justify-content: center;
                        align-items: flex-end;

                        .proceedBtn {
                            width: 105px;
                            height: 45px;
                            align-items: center;
                            border-radius: 36px;
                            background: #4367B0;
                            color: #FFF;
                            text-align: center;
                            font-size: 16px;
                            font-weight: 500;
                            letter-spacing: 0.2px;
                            border: none;
                            justify-content: center;
                            display: flex;
                            gap: 10px;
                        }

                        .clearBtn {
                            background: #fff;
                            border: 1px solid #4367B0;
                            color: #4367B0;
                        }
                    }

                }
            }


            .adminDashboardContainer {
                border-radius: 14px;
                border: 1px solid #E6E6E6;
                background-color: #FFF;
                padding: 25px;
                display: flex;
                flex-direction: column;
                gap: 20px;
                margin-top: 20px;

                 .bodyNavigation {
                    border-radius: 16px;
                    display: inline-flex;

                    .title {
                        padding: 12px 20px;
                        border-right: 1px solid #4367B0;
                        font-size: 16px;
                        font-weight: 500;
                        border: 1px solid #4367B0;
                        cursor: pointer;
                    }

                    .title:first-child {
                        border-radius: 16px 0 0 16px;
                        border-right: 0;

                    }

                    .title:last-child {
                        border-radius: 0 16px 16px 0;
                        border-left: 0;
                    }

                    .active {
                        color: #fff;
                        background-color: #4367B0;
                    }
                }

                .statsBoxContainer {
                    // display: flex;
                    // flex-wrap: wrap;
                    // gap: 10px;
                    // justify-content: space-between;
                    // align-items: center;
                    width: 100%;
                    display: grid;
                    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
                    gap: 10px;

                    .statsBox {
                        border: 1px solid #BFBFBF;
                        padding: 28px 10px;
                        display: flex;
                        border-radius: 14px;
                        gap: 20px;
                        justify-content: space-between;
                        align-items: center;
                        width: 100%;
                        place-items: center;

                        .left {
                            width: 60%;
                            display: flex;
                            flex-direction: column;
                            gap: 30px;
                            justify-content: center;
                            align-items: center;

                            .statTitle {
                                font-size: 20px;
                                font-weight: 500;
                                white-space: nowrap;
                            }

                            .statNumber {
                                font-size: 28px;
                                font-weight: 600;
                                color: #4367B0;
                            }
                        }

                        .left:last-child {
                            width: 100%;
                        }

                        .progressCircle {
                            width: 40%;
                            display: flex;
                            align-items: center;

                            .CircularProgressbar-path {
                                stroke: #4367B0;
                            }

                            .CircularProgressbar-trail {
                                stroke: #D0D9EB;
                            }

                            .CircularProgressbar-text {
                                fill: #4367B0;
                            }

                            .CircularProgressbar-background {
                                fill: #4367B0;
                            }
                        }

                    }
                }

                .linearChat {
                    width: 100%;
                    height: 400px;
                }
            }



        }
    }
}