.myTasks {
    .myTaskTable {
        border-radius: 20px;
        border: 1px solid #808080;
        table {
            border-collapse: collapse;
            border-spacing: 0;
            width: 100%;
        }

        tr th {
            color: #808080;
            font-size: 16px;
            font-weight: 500;
            letter-spacing: 0.16px;
            padding: 16px 20px;
            white-space: nowrap;

        }

        tr td {
            color: #000;
            font-size: 16px;
            font-weight: 400;
            letter-spacing: 0.16px;
            padding: 16px 20px;
            white-space: nowrap;

        }

        th,
        td {
            text-align: left;
            padding: 8px;

        }

        tr {
            border-bottom: 1px solid #BFBFBF;

        }

        .tableBtn {
            color: #FFF;
            font-size: 16px;
            font-weight: 500;
            letter-spacing: 0.16px;
            background-color: #4367B0;
            border-radius: 8px;
            padding: 0 10px;
            height: 45px;
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 5px;
            border: none;
        }

    }
}