.checklist {
    .questionContainer {
        display: flex;
        flex-direction: column;
        gap: 30px;

        .questionItem {
            display: flex;
            gap: 40px;
            width: 100%;
            border-bottom: 1px solid #BFBFBF;
            padding: 30px 0;

            .question {
                font-size: 20px;
                font-weight: 600;
                width: 25%;
            }

            .filterContainer {
                display: flex;
                flex-direction: column;
                gap: 20px;
                width: 30%;

                .searchField {
                    position: relative;
      
                    .searchDropdown {
                      position: absolute;
                      width: 100%;
                      background-color: #fff;
                      border: 1px solid #808080;
                      top: 40px;
                      border-radius: 10px;
      
                      max-height: 250px;
                      overflow: auto;
      
                      p {
                        color: #000;
                        padding: 5px 10px;
                        border-bottom: 1px solid #bfbfbf;
                        font-size: 14px;
                      }
      
                      p:last-child {
                        border-bottom: none;
      
                      }
      
                      p:hover {
                        background-color: #f0f0f0;
                      }
                    }
                  }

                .formInput {
                    width: 100%;

                    label {
                        color: #808080;
                        font-size: 16px;
                        font-weight: 500;
                        letter-spacing: 0.16px;

                        span {
                            color: #D80032;
                        }
                    }

                    input,select {
                        color: #4367B0;
                        width: 100%;
                        padding: 13px 24px;

                        border: 1px solid #BFBFBF;
                        border-radius: 36px;
                        background: #FFF;
                        font-size: 16px;
                        font-weight: 400;
                        letter-spacing: 0.16px;
                        margin-top: 5px;
                    }

                    input::placeholder {
                        color: #BFBFBF;
                    }

                    input:focus {
                        outline: 1px solid #4367B0;
                    }
                }

                .filters {
                    display: flex;
                    flex-wrap: wrap;
                    gap: 10px;

                    .filterItem {

                        display: flex;
                        align-items: center;
                        justify-content: center;
                        padding: 10px;
                        font-size: 14px;
                        color: #4367B0;
                        border-radius: 40px;
                        border: 1px solid #4367B0;
                        gap: 5px;

                    }
                }
            }
        }
    }

    .nextButton {
        border-radius: 52px;
        background: var(--Primary-100, #4367B0);
        color: var(--White-100, #FFF);
        text-align: center;
        font-size: 20px;
        font-weight: 500;
        letter-spacing: 0.2px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        border: none;
        width: 150px;
        height: 56px;
        margin-top: 20px;
    }
}