.roles {
  .dashboardBody {
    display: flex;

    .dashboardContent {
      padding: 20px 15px;
      width: calc(100% - 230px);

      .headerBody {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .actionHeader {
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 20px;
        }
        .tableBtn {
          color: #fff;
          font-size: 16px;
          font-weight: 500;
          letter-spacing: 0.16px;
          background-color: #4367b0;
          padding: 0 10px;
          height: 45px;
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 5px;
          border: none;
          border-radius: 35px;
        }

        .title {
          color: #000;
          font-size: 28px;
          font-weight: 600;
          letter-spacing: 0.28px;
        }
      }

      .formInput {
        label {
          color: #808080;
          font-size: 16px;
          font-weight: 500;
          letter-spacing: 0.16px;

          span {
            color: #d80032;
          }
        }

        input,
        select {
          color: #4367b0;
          width: 100%;
          padding: 13px 24px;

          border: 1px solid #bfbfbf;
          border-radius: 36px;
          background: #fff;
          font-size: 16px;
          font-weight: 400;
          letter-spacing: 0.16px;
          margin-top: 5px;
        }

        input::placeholder {
          color: #bfbfbf;
        }

        input:focus {
          outline: 1px solid #4367b0;
        }
      }

      .rolesContainer {
        border-radius: 14px;
        // border: 1px solid #E6E6E6;
        background-color: #fff;
        // padding: 25px;
        display: flex;
        flex-direction: column;
        gap: 20px;
        margin-top: 20px;

        .employeeDetails {
          border-radius: 15px;
          border: 1px solid var(--Secondary-25, #bfbfbf);
          width: 100%;

          table {
            border-collapse: collapse;
            border-spacing: 0;
            // width: 100em;
            width: 100%;
          }

          thead {
            tr th {
              color: #808080;
              padding: 16px 12px;
              font-size: 16px;
              font-weight: 400;
              letter-spacing: 0.2px;
              white-space: nowrap;
              text-align: left;
              border: none !important;
              white-space: nowrap;
            }

            tr {
              border-bottom: 1px solid var(--Secondary-25, #bfbfbf);
            }
          }

          tbody {
            tr td {
              color: #000;
              font-size: 16px;
              font-weight: 400;
              letter-spacing: 0.16px;
              white-space: nowrap;
              padding: 20px 12px;
              text-align: left;
              white-space: nowrap;
            }

            tr:nth-child(odd) {
              background-color: #f4f6fb;
            }

            tr {
              border-bottom: 1px solid var(--Secondary-25, #bfbfbf);
            }

            tr:last-child {
              border-bottom: none;
            }

            .employeeTable {
              width: 100%;
            }

            .actionLink a {
              color: #4367b0;
              font-weight: 600;
            }
          }
        }
      }
    }
  }
}
