.permissionManagement {
    .permissionManagementBody {
        margin-top: 40px;
        padding: 40px;
        display: flex;
        flex-direction: column;

        h2 {
            font-size: 28px;
            font-weight: 700;
        }

        .topNavigation {
            display: flex;
            border-radius: 10px;

            .item {
                color: #A1B3D8;
                font-size: 22px;
                font-weight: 500;
                letter-spacing: 0.16px;
                padding: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
                position: relative;
            }

            .active::after {
                content: "";
                width: 60%;
                height: 5px;
                position: absolute !important;
                bottom: 0;
                display: block;
                background-color: #4367B0;
                border-radius: 10px 10px 0 0;
            }

            .active {
                color: #4367B0;
            }
        }


        .permissionManagementContainer {
            border-radius: 14px;
            border: 1px solid #e6e6e6;
            background-color: #fff;
            padding: 25px;
            display: flex;
            flex-direction: column;
            gap: 20px;


            .bodyNavigation {
                border-radius: 16px;
                display: inline-flex;

                .title {
                    padding: 12px 20px;
                    border-right: 1px solid #4367B0;
                    font-size: 16px;
                    font-weight: 500;
                    border: 1px solid #4367B0;
                    cursor: pointer;
                }

                .title:first-child {
                    border-radius: 16px 0 0 16px;
                    border-right: 0;

                }

                .title:last-child {
                    border-radius: 0 16px 16px 0;
                    border-left: 0;
                }

                .active {
                    color: #fff;
                    background-color: #4367B0;
                }
            }



        }

    }
}