.processChecklist {
    .permissionManagement {
        .permissionManagementBody {
            margin-top: 40px;
            padding: 40px;
            display: flex;
            flex-direction: column;
            gap: 20px;
    
            h2 {
                font-size: 28px;
                font-weight: 700;
            }

     


    
            .topNavigation {
                display: flex;
                border-radius: 10px;
    
                .item {
                    background-color: #A1B3D8;
                    color: var(--White-100, #FFF);
                    font-size: 16px;
                    font-weight: 500;
                    letter-spacing: 0.16px;
                    padding: 20px;
                    width: 160px;
                    height: 50px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
    
                .active {
                    background-color: #4367B0;
                }
            }
    
    
            .permissionManagementContainer {
                border-radius: 14px;
                border: 1px solid #e6e6e6;
                background-color: #fff;
                padding: 25px;
                display: flex;
                flex-direction: column;
                gap: 20px;
                margin: 20px 0;
    
    
                .bodyNavigation {
                    border-radius: 16px;
                    display: inline-flex;
    
                    .title {
                        padding: 12px 20px;
                        border-right: 1px solid #4367B0;
                        font-size: 16px;
                        font-weight: 500;
                        border: 1px solid #4367B0;
                        cursor: pointer;
                    }
    
                    .title:first-child {
                        border-radius: 16px 0 0 16px;
                        border-right: 0;
    
                    }
    
                    .title:last-child {
                        border-radius: 0 16px 16px 0;
                        border-left: 0;
                    }
    
                    .active {
                        color: #fff;
                        background-color: #4367B0;
                    }
                }
    
    
    
            }
    
        }
    }
    .dashboardBody {
        display: flex;



        .dashboardContent {
            padding: 20px 15px;
            width: calc(100% - 230px);


            .headerBody {
                display: flex;
                justify-content: space-between;
                align-items: center;
                .tableBtn {
                    color: #FFF;
                    font-size: 16px;
                    font-weight: 500;
                    letter-spacing: 0.16px;
                    background-color: #4367B0;
                    padding: 0 10px;
                    height: 45px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 5px;
                    border: none;
                    border-radius: 35px;
                }

                .title {
                    color: #000;
                    font-size: 28px;
                    font-weight: 600;
                    letter-spacing: 0.28px;
                }
            }

            .title {
                color: #000;
                font-size: 28px;
                font-weight: 600;
                letter-spacing: 0.28px;
            }
            
            .searchContainer {
                    background-color: #FFF;
                    display: flex;
                    flex-direction: column;
                    gap: 20px;
                    margin-top: 20px;
                .formFields {
                    display: flex;
                    gap: 20px;
                    // width: 100%;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    width: 100%;
    
                    .formInput {
                        // width: 47%;
                    }
    
    
                    label {
                        color: #808080;
                        font-size: 16px;
                        font-weight: 500;
                        letter-spacing: 0.16px;
    
                        span {
                            color: #D80032;
                        }
                    }
    
                    input,select {
                        color: #4367B0;
                        width: 100%;
                        padding: 13px 24px;
    
                        border: 1px solid #BFBFBF;
                        border-radius: 36px;
                        background: #FFF;
                        font-size: 16px;
                        font-weight: 400;
                        letter-spacing: 0.16px;
                        margin-top: 5px;
                    }
    
                    input::placeholder {
                        color: #BFBFBF;
                    }
    
                    input:focus {
                        outline: 1px solid #4367B0;
                    }

                    
                .searchBtns {
                    display: flex;
                    gap: 30px;
                    justify-content: center;
                    align-items: flex-end;

                    .proceedBtn {
                        width: 105px;
                        height: 45px;
                        align-items: center;
                        border-radius: 36px;
                        background: #4367B0;
                        color: #FFF;
                        text-align: center;
                        font-size: 16px;
                        font-weight: 500;
                        letter-spacing: 0.2px;
                        border: none;
                        justify-content: center;
                        display: flex;
                        gap: 10px;
                    }
                    .clearBtn {
                        background: #fff;
                        border: 1px solid #4367B0;
                        color: #4367B0;
                    }
                }
    
                }
            }

            .processChecklistContainer {
                border-radius: 14px;
                border: 1px solid #E6E6E6;
                background-color: #FFF;
                padding: 25px;
                display: flex;
                flex-direction: column;
                gap: 20px;
                margin-top: 20px;


                .bodyNavigation {
                    border-radius: 16px;
                    display: inline-flex;
    
                    .title {
                        padding: 12px 20px;
                        border-right: 1px solid #4367B0;
                        font-size: 16px;
                        font-weight: 500;
                        border: 1px solid #4367B0;
                        cursor: pointer;
                        color: #4367B0;
                    }
    
                    .title:first-child {
                        border-radius: 16px 0 0 16px;
                        border-right: 0;
    
                    }
    
                    .title:last-child {
                        border-radius: 0 16px 16px 0;
                        border-left: 0;
                    }
    
                    .active {
                        color: #fff;
                        background-color: #4367B0;
                    }
                }
            }


  
        }
    }
}