.addRoles {
    .addRolesBody {
        margin-top: 40px;
        padding: 40px;
        display: flex;
        flex-direction: column;

        h2 {
            font-size: 28px;
            font-weight: 700;
        }
        .topNavigation {
            display: flex;
            border-radius: 10px;

            .item {
                color: #A1B3D8;
                font-size: 22px;
                font-weight: 500;
                letter-spacing: 0.16px;
                padding: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
                position: relative;
            }

            .active::after {
                content: "";
                width: 60%;
                height: 5px;
                position: absolute !important;
                bottom: 0;
                display: block;
                background-color: #4367B0;
                border-radius: 10px 10px 0 0;
            }

            .active {
                color: #4367B0;
            }
        }
        .addRolesContainer {
            border-radius: 14px;
            border: 1px solid #bfbfbf;
            background-color: #FFF;
            padding: 25px;
            display: flex;
            flex-direction: column;
            gap: 40px;

            .addRolesContent {
                display: flex;
                justify-content: space-between;
                gap: 40px;

            }

            .formActions {
                display: flex;
                align-items: flex-end;
                justify-content: flex-start;
                gap: 30px;

                .add, .delete {
                    color: #4367B0;
                    font-size: 16px;
                    font-weight: 500;
                    display: flex;
                    gap: 5px;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                }

                .delete {
                    color: #D80032;
                }
            }



            .formFields {
                display: flex;
                gap: 20px;
                // width: 100%;
                flex-wrap: wrap;
                justify-content: space-between;
                width: 60%;

                .formInput {
                    width: 47%;
                }

                label {
                    color: #808080;
                    font-size: 16px;
                    font-weight: 500;
                    letter-spacing: 0.16px;

                    span {
                        color: #D80032;
                    }
                }

                input {
                    color: #4367B0;
                    width: 100%;
                    padding: 13px 24px;

                    border: 1px solid #BFBFBF;
                    border-radius: 36px;
                    background: #FFF;
                    font-size: 16px;
                    font-weight: 400;
                    letter-spacing: 0.16px;
                    margin-top: 5px;
                }

                input::placeholder {
                    color: #BFBFBF;
                }

                input:focus {
                    outline: 1px solid #4367B0;
                }

            }

            .nextBtn {
                border-radius: 52px;
                background: var(--Primary-100, #4367B0);
                color: var(--White-100, #FFF);
                text-align: center;
                font-size: 20px;
                font-weight: 500;
                letter-spacing: 0.2px;
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 10px;
                border: none;
                width: 150px;
                height: 56px;
            }



        }


    }
}